.mind-map-container {
  position: relative;
}

.center-image {
  display: block;
  margin: 0 auto;
}

.mind-map-item {
  position: absolute;
}

.connecting-line {
  position: absolute;
  background-color: white;
  width: 2px;
  height: 0;
  transition: height 0.5s ease-in-out;
}

#line1 {
  top: -200px; /* Adjust the positioning based on your layout */
  left: 50%;
  transform: translateX(-50%);
}

#line2 {
  top: 50%;
  right: -200px; /* Adjust the positioning based on your layout */
  transform: translateY(-50%);
}

#line3 {
  bottom: -200px; /* Adjust the positioning based on your layout */
  left: 50%;
  transform: translateX(-50%);
}

#line4 {
  top: 50%;
  left: -200px; /* Adjust the positioning based on your layout */
  transform: translateY(-50%);
}

#line5 {
  top: -200px; /* Adjust the positioning based on your layout */
  left: 50%;
  transform: translateX(-50%);
}

#item1 {
  top: -250px;
  left: 0;
}
#item2 {
  top: 0;
  right: 0;
}
#item3 {
  bottom: 0;
  left: 0;
}
#item4 {
  bottom: 0;
  right: 0;
}
#item5 {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* 타이핑 애니메이션 속도 설정 */
@keyframes typing {
  from {
    width: 0;
  }
}

/* 타이핑 효과 스타일 설정 */
#typing-text::after {
  content: "|";
  display: inline-block;
  vertical-align: middle;
  margin-left: 5px; /* 커서 위치 조정 */
  animation: typing 0.8s steps(40, end),
    blink-caret 0.5s step-end infinite alternate;
}

/* 커서 깜빡임 효과 설정 */
@keyframes blink-caret {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: white;
  }
}

#typing-text.typing-complete {
  opacity: 0;
  transition: opacity 1s ease-in-out;
}

#border-gradient-to-r {
  border-image: linear-gradient(to right, #68d391, #9e5eff, #bdbdbd) 1;
  border-radius: 30%;
}

@keyframes slideIn {
  from {
    transform: translateY(-20%);
  }
  to {
    transform: translateY(0);
  }
}

.animate-slide-in {
  animation: slideIn 1.5s ease-out;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.animated-gradient {
  animation: gradientAnimation 5s linear infinite; /* 애니메이션 이름, 시간, 타이밍 함수, 반복 설정 */
}
